* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
  font-size: 100px;
  color: #fff;
}

.title {
  color: #000000;
}

.squares {
  display: flex;
  flex-wrap: wrap;
}

.square__block {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 300px;
  height: 300px;
  background-color: #ff0000;
}

.square__block:nth-child(2) {
  background-color: #cc0000;
}

.square__block:nth-child(3) {
  background-color: #990000;
}

.square__block:nth-child(4) {
  background-color: #660000;
  order: 6;
}

.square__block:nth-child(5) {
  background-color: #330000;
  order: 5;
}

.square__block:nth-child(6) {
  background-color: #000000;
  order: 4;
}
